@import "./variables";

:root {
  --img-index: 0;
}

.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolio__display {
  flex-shrink: 0;

  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: 2fr 1fr 2fr;
  position: relative;
  // width: clamp(280px, 100vw, 800px);
  width: clamp(250px, 100%, 1000px);
  aspect-ratio: 11/16;
  margin-bottom: 1em;
  border: 5px solid black;
  overflow: hidden;
  border-radius: 8px;

  // box-shadow: 5px 5px 0 black, -5px -5px 0 0 #444;
}

.portfolio__slider {
  display: flex;
  transform: translateX(calc(-100% * var(--img-index)));
  transition: transform 0.2s;
  grid-column: 1/4;
  grid-row: 1/4;
}

.portfolio__display img,
.portfolio__display picture {
  flex-grow: 0;
  flex-shrink: 0;
  display: block;
  width: 100%;
  aspect-ratio: 11/16;
  object-fit: cover;
  object-position: left top;
}

.portfolio__arrow {
  grid-column: 1/2;
  grid-row: 2/3;
  justify-self: start;
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  // padding-top: 20%;
  height: 100%;
  color: white;
  cursor: pointer;
  z-index: 1;
  // transition: all 0.3s;
  filter: drop-shadow(7px 7px 3px rgb(0, 0, 0, 0.9));
}
.portfolio__arrow--next {
  grid-column: 3/4;
  justify-self: end;
}

.portfolio__a-buttons {
  display: flex;
  gap: 1ch;
  grid-column: 3/4;
  grid-row: 1/2;
  align-self: start;
  margin: 1ch;
  z-index: 1;
}

.portfolio__arrow svg {
  width: clamp(30px, 10vw, 80px);
  height: clamp(30px, 10vw, 80px);
}

.portfolio__dots {
  display: flex;
  // position: absolute;
  justify-self: center;
  align-self: end;
  // background-color: red;
  grid-column: 1/4;
  grid-row: 3/4;
  margin-bottom: 10px;
  gap: clamp(10px, 4vw, 20px);
  z-index: 1;
}

.portfolio__dot {
  display: inline-block;
  height: clamp(4px, 6vw, 15px);
  width: clamp(4px, 6vw, 15px);
  background-color: white;
  border: none;
  border-radius: 9999vw;
  // box-shadow: 0 0 0px 1px black;
  cursor: pointer;
  box-shadow: 0 2px 5px black;
  transition: background-color 0.2s;
}

.portfolio__dot:hover {
  background-color: #aaa;
}

.portfolio__dot--current,
.portfolio__dot--current:hover {
  background-color: black;
  cursor: default;
}

.portfolio__description {
  padding: 0 1ch;
  display: block;
  min-height: 10em;
  font-size: 18px;
  // font-weight: 600;
  // font-size: 1px;
  // width: 50%;
  // text-indent: 1ch;
  // max-height: fit-content;
  // height: fit-content;
  // transition: max-height 0.3s;
}

@media only screen and (min-width: 500px) {
  .portfolio__display {
    aspect-ratio: 16/9;
  }

  .portfolio__display img,
  .portfolio__display picture {
    aspect-ratio: 16/9;
  }
}

@media only screen and (min-width: 1000px) {
  .portfolio {
    flex-direction: row;
    align-items: start;
  }

  .portfolio__display {
    width: clamp(800px, 70vw, 1000px);
    margin-bottom: 3em;
  }
}
