@use "./portfolio";
@use "./skills";
@use "./header";
@import "./variables";

body,
html {
  min-width: 280px;
  margin: 0;
  font-family: "Quicksand", sans-serif;
}

p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

h2 {
  padding: 1em 0;
  font-size: 2em;
  font-weight: 600;
}

header,
footer,
section {
  --padding: clamp(20px, 3vw, 100px);
  padding-left: var(--padding);
  padding-right: var(--padding);
  // border: 1px solid red;
}

.round-a-btn {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;

  height: 3em;
  width: 3em;
  border: 3px solid $font-light;
  border-radius: 999px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

  background-color: $bg-dark;
  fill: $font-light;
  box-shadow: 5px 0px 3px black;
}

.round-a-btn span {
  position: absolute;
  background-color: black;
  bottom: -1em;
  width: 100%;
  font-size: 14px;
  color: white;
  text-align: center;
  border-bottom: 3px solid $font-light;
  border-left: 3px solid $font-light;
  border-right: 3px solid $font-light;

  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 5px 5px 3px black;
}

.round-a-btn svg {
  width: 2em;
}
